@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,200;6..12,300;6..12,400;6..12,500;6..12,600;6..12,700;6..12,800;6..12,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@700&display=swap');



body {
    font-family: $font-text;
}
img {
    width: 100%;
}

.container {
    max-width: 1180px;
    margin: 0 auto;
    padding: 0 15px;
}
.btn-green {
height: 44px;
padding: 0 24px;
border-radius: 4px;
background: rgba(26, 62, 62, 1);

font-size: 16px;
font-weight: 600;
color: rgba(253, 253, 253, 1);
cursor: pointer;
}

.btn-white {
    height: 52px;
    padding: 0 36px;
    border-radius: 4px;
    background: rgba(253, 253, 253, 1);

font-size: 16px;
font-weight: 700;
color: rgba(26, 62, 62, 1);
cursor: pointer;


}