body {
  font-family: "Nunito Sans", sans-serif;
}

img {
  width: 100%;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn-green {
  height: 44px;
  padding: 0 24px;
  border-radius: 4px;
  background: rgb(26, 62, 62);
  font-size: 16px;
  font-weight: 600;
  color: rgb(253, 253, 253);
  cursor: pointer;
}

.btn-white {
  height: 52px;
  padding: 0 36px;
  border-radius: 4px;
  background: rgb(253, 253, 253);
  font-size: 16px;
  font-weight: 700;
  color: rgb(26, 62, 62);
  cursor: pointer;
}

.header {
  background-image: url("/img/img/header_bg.png");
  background-repeat: no-repeat;
  padding-top: 16px;
  padding-bottom: 80px;
}

.header-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-nav__menu {
  display: flex;
  gap: 24px;
}
.header-nav__menu-link {
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  color: rgb(253, 253, 253);
}

.header-content {
  font-family: "Titillium Web", sans-serif;
  font-size: 50px;
  text-transform: uppercase;
  padding-top: 463px;
  padding-bottom: 41px;
  color: rgb(253, 253, 253);
}

.header-form {
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.2);
}

.header-form__wrapper {
  display: flex;
  justify-content: space-around;
  padding-top: 40px;
  padding-bottom: 40px;
}

.header-form__group-select {
  width: 270px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 14px 16px 14px 16px;
  margin-bottom: 8px;
  cursor: pointer;
}

.header-form__group-select-text {
  font-size: 16px;
  font-weight: 700;
  color: rgb(253, 253, 253);
}

.header-form__select-text--option {
  color: black;
  background: gray;
}

.header-form__group-text {
  font-size: 14px;
  font-weight: 400;
  color: rgb(253, 253, 253);
}

input::placeholder {
  color: rgb(253, 253, 253);
}

.no_text_into_input {
  opacity: 0;
}

